body {
	margin: 0;
	padding: 0;

	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

.app{background-color:#4e2f2f;padding-top:90px}.movies{display:inline-flex;justify-content:center;margin-left:5%}.jumbotron{background:url(/static/media/24.a8c1c7d4.jpg),linear-gradient(purple 0%, #940000 80%);text-align:center;margin-top:-50px;height:100vh;overflow:hidden;color:#fff;background-size:cover;background-blend-mode:multiply}.jumbotron h1{position:absolute;margin-left:20%;margin-right:20%}.particle{position:absolute}#search{position:fixed;height:384px;margin-right:-27px;color:red;width:100%;width:280px;overflow-y:scroll;right:0px;top:67px}::-webkit-scrollbar{width:15px}::-webkit-scrollbar-track{background-color:#4e2f2f;border-radius:0}::-webkit-scrollbar-thumb{border-radius:10px;background:#c02d39}#loadMore{width:100%;margin-bottom:-20px;background-color:#c02d39;text-align:center;cursor:pointer}footer{height:300px;background:url(/static/media/24.a8c1c7d4.jpg),linear-gradient(to right, purple 0%, #940000 80%);background-size:cover;background-blend-mode:multiply}

#search{background-color:transparent;border:none}#search :focus{background-color:transparent}#sidebar{margin-top:65px;width:-webkit-min-content;width:-moz-min-content;width:min-content;display:block;height:100vh;background-color:rgba(17,16,16,0.76)}.sidenavlinks{color:white}.sidenavlinks :focus{background-color:#222b31}

.card_image{background:linear-gradient(#fafa03, #740f0f);width:210px;height:310px;cursor:pointer;transition:0.4s;object-fit:cover;border:4px solid rgba(240,240,240,0.884);border-radius:inherit;margin:-1px 0 0 -1px}.overlay{border-radius:inherit;margin:0;margin:initial;position:absolute;bottom:0;left:100%;z-index:2;right:0;background-color:rgba(119,28,28,0.589);overflow:hidden;width:10%;height:100%;transition:0.3s ease}.text{color:red;font-size:20px;text-align:center;text-overflow:clip;font-weight:800;position:absolute;top:40%;left:50%;transform:translate(-40%, -50%);white-space:nowrap}.card_image:hover .overlay{width:100%;left:0}.card_title{text-align:left;display:block;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;font-weight:bold;font-size:16px;width:230px}.title-white{color:#0c0b0b}.kaya{color:#d4d4d4 smoke}

